/*eslint-disable*/
import logo from './logo.svg';
import './App.css';
import data from './data';

// or less ideally
import React, { useState, lazy, Suspense } from 'react';
import { Route, Switch } from 'react-router-dom';
import axios from 'axios';

// component
import Navbar1 from './component/Navbar1';
import Jumbotron from './component/Jumbotron';
// import Detail from './component/Detail';
const Detail = lazy(() => {
  return import('./component/Detail');
});

import Card from './component/Card';

// import Cart from './component/Cart';
const Cart = lazy(() => {
  return import('./component/Cart');
});

import Forms from './component/Forms';
import { useSelector } from 'react-redux';
import Recently from './component/Recently';
// context API
export let 재고context = React.createContext();

function App() {
  const [신발들, 신발들변경] = useState(data);

  const [재고, 재고변경] = useState([10, 11, 12, 10, 11, 12]);

  const [로딩, 로딩변경] = useState(false);

  const [버튼, 버튼변경] = useState(true);

  const [findsItem, setFindItem] = useState([]);

  const 신발목록 = useSelector((state) => state.reducer3);

  const newData = () => {
    //로딩중이라는 ui 띄움
    로딩변경(true);
    //서버에게 보냄 ex) 로그인등
    // axios.post('서버url', { id: 'won', pw: 123 });
    버튼변경(false);
    axios
      .get('https://codingapple1.github.io/shop/data2.json')
      .then((result) => {
        //로딩중이라는 ui 안보이게 처리
        로딩변경(false);
        신발들변경([...신발들, ...result.data]);
      })
      .catch(() => {
        console.log('실패');
      });
  };

  // const [age, setage] = useState(20);

  // const [count, setcount] = useState(0);

  // useEffect(() => {
  //   if (count != 0 && count < 3) {
  //     setage(age + 1);
  //   }
  // }, [count]);

  return (
    <div className="App">
      {/* header */}
      <Navbar1 />
      {/* <div>
        <div>안녕하십니까 전 {age}</div>
        <button
          onClick={() => {
            setcount(count + 1);
          }}
        >
          누르면 한살먹기
        </button>
      </div> */}
      {/* main */}
      <Switch>
        <Route exact path="/">
          {/* <Recently findsItem={findsItem} setFindItem={setFindItem} /> */}

          <Jumbotron />

          <p>{신발목록[0].title}</p>

          <Forms 신발들={신발들} 신발들변경={신발들변경} />

          <div className="container">
            <div className="row">
              {신발들.map((신발, i) => {
                return <Card 신발들={신발들[i]} key={i} />;
              })}
            </div>

            {로딩 && <h1>로딩중입니다</h1>}

            {버튼 && (
              <button className="btn btn-primary" onClick={newData}>
                더보기
              </button>
            )}
          </div>
        </Route>

        <Suspense fallback={<div>로딩중이에요</div>}>
          <재고context.Provider value={재고}>
            <Route path="/detail/:id">
              <Detail 신발들={신발들} 재고={재고} 재고변경={재고변경} findsItem={findsItem} setFindItem={setFindItem} />
            </Route>
          </재고context.Provider>

          <Route path="/cart" component={Cart}></Route>
        </Suspense>
      </Switch>
    </div>
  );
}

export default App;
